import React from "react";
import { Form, Input, Grid, Divider } from "antd-mobile";
import { AddCircleOutline } from "antd-mobile-icons";
import { ExerciseSet } from "./exercise-set";
import { DeleteExerciseButton } from "./delete-exercise-button";

export const Exercises = () => {
	return (
		<Form.Array
			name="exercises"
			renderAdd={() => (
				<span>
					<AddCircleOutline /> Add exercise
				</span>
			)}
		>
			{(fields, { remove }) =>
				fields.map(({ index }) => (
					<>
						<Grid columns={4}>
							<Grid.Item span={3}>
								<Form.Item
									name={[index, "name"]}
									rules={[
										{
											required: true,
											message: "Name is required",
										},
										{
											type: "string",
											min: 2,
											message: "Too short",
										},
										{
											type: "string",
											max: 100,
											message: "Too long",
										},
									]}
								>
									<Input placeholder="Exercise name" />
								</Form.Item>
							</Grid.Item>
							<Grid.Item>
								<DeleteExerciseButton
									onClick={() => remove(index)}
								/>
							</Grid.Item>
						</Grid>
						<Divider />
						<Form.Array
							name={[index, "sets"]}
							renderAdd={() => (
								<span>
									<AddCircleOutline /> Add set
								</span>
							)}
						>
							{(fields, { remove }) =>
								fields.map((field) => (
									<ExerciseSet
										field={field}
										onRemoveSet={() => remove(field.index)}
									/>
								))
							}
						</Form.Array>
					</>
				))
			}
		</Form.Array>
	);
};
